import React, { Component } from 'react';
import axios from "axios";
import Config from "../BaseUrl/Config";

class NewPassword extends Component {
    constructor() {
        super()
        this.onChangeOTP = this.onChangeOTP.bind(this);
        this.onChangeConfirmPass = this.onChangeConfirmPass.bind(this);
        this.onChangeNewPass = this.onChangeNewPass.bind(this);
        this.onSubmit = this.onSubmit.bind(this);


        this.state = {
            Otp: null,
            newPass: null,
            confPass: null,
            successalart: false,
            alertSt:''
        }
    }

    onChangeOTP(e) {
        this.setState({
            Otp: e.target.value
        });
    }
    onChangeNewPass(e) {
        this.setState({
            newPass: e.target.value
        });
    }
    onChangeConfirmPass(e) {
        this.setState({
            confPass: e.target.value
        });
    }
    onSubmit(e) {
        e.preventDefault();
        const obj = {
            newPassword: this.state.newPass,
            conformPassword: this.state.confPass,
            forgotCode: this.state.Otp
        };
        axios.put(`${Config.apiUrl}/api/forgot-password`, obj)
            .then((res) => {
                console.log(res)
                this.setState({
                    email: '',
                    successalart: true,
                    alertSt:'Password Update Successfully'

                })
                // () => { window.setTimeout(() => { this.setState({ successalart: false }) }, 2000) })
            })
            .catch(error => {
                if (error.response) {
                    this.setState({
                        error_message: error.response.data.errors,
                    })
                }
            }
            );
    }

    render() {
        return (
            <React.Fragment>
                <div class="container mt-4">
                    <div class="row">
                        {/* <!-- post start  --> */}
                        <div class="askqst_post_wrap profile_img">
                            <div class="row box_shado row_div">
                                <div class="col-sm-6 pass_div">

                                    <img src="https://efrana.github.io/Tech_blogV1.2UI/img/pass3.jpg" class="img-responsive pass_img"
                                        alt="Logo" />

                                </div>
                                <div class="col-sm-6 pass_div ">
                                    <div class="heading">
                                        <h4><strong>Reset Password</strong></h4>
                                    </div>
                                    <form>
                                    <div class="forgot_pass">
                                        <div class="form-group">
                                            <input type="username" class="form-control fontcontrol" id="text"
                                                placeholder="Enter OTP Code here.." name="userName"
                                                onChange={this.onChangeOTP}
                                                value={this.state.Otp}
                                            />
                                        </div>
                                        <div class="input-group margin-bottom-sm " style={{ marginBottom: "10px" }}>
                                            <span class="input-group-addon"><i class="fa fa-lock fa-fw"></i></span>
                                            <input class="form-control fontcontrol " type="text" placeholder="New Password"
                                                onChange={this.onChangeNewPass}
                                                value={this.state.newPass}
                                            />
                                        </div>
                                        <div class="input-group margin-bottom-sm ">
                                            <span class="input-group-addon"><i class="fa fa-lock fa-fw"></i></span>
                                            <input class="form-control fontcontrol" type="text" placeholder="Confirm Password"
                                                onChange={this.onChangeConfirmPass}
                                                value={this.state.confPass}
                                            />
                                        </div>
                                        <br />
                                        <button type="submit" class="btn btn-success addButton btn_widh" onClick={this.onSubmit}>CHANGE PASSWORD</button>
                                        {this.state.alertSt}

                                    </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                        {/* <!-- post end  --> */}
                    </div>
                </div>

            </React.Fragment>
        )
    }

}
export default NewPassword