import React, { Component } from 'react';
import { Link, Redirect, withRouter } from 'react-router-dom';
import SearchBar from "./SearchBar"
import Login from '../Pages/Login';
import Signup from '../Pages/Signup';
import Dropdown from './Dropdown';

class NavBar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			IsAuthenticated: false,
			ModalOpen: false
		};
	}

	logout = () => {
		sessionStorage.clear();
		localStorage.clear();
		this.setState({
			IsAuthenticated: false
		})
	}


	componentDidMount() {
		setInterval(() => {
			if (window.localStorage.getItem('jwt-token')) {
				this.setState({
					IsAuthenticated: true
				})
			}

		}, 1000);
	}
	openModal = () => this.setState({ ModalOpen: !this.state.ModalOpen});




	render() {
		let authenticationMenu
		if (!this.state.IsAuthenticated) {
			authenticationMenu =
				<React.Fragment>
					<li>
						<a href="#" data-toggle="modal" data-target="#mymodal" onClick={this.openModal} >Login</a>
					</li>
					<li>
						<a href="#" className="btn  btn-sm btn-user" data-toggle="modal" data-target="#signupmodal" onClick={this.openModal} >Sign
						Up
					   </a>
					</li>
				</React.Fragment>

		} else {
			authenticationMenu =
				<React.Fragment>

					<Dropdown
						logout={() => {

							this.logout()
						}}
					/>
				</React.Fragment>
		}
		return (
			<React.Fragment>
				<nav className="navbar navbar-default  nav-top-bg" role="navigation">
					<div className="container">
						<div className="row">
							<div className="navbar-header">
								<button type="button" className="navbar-toggle" data-toggle="collapse"
									data-target="#bs-example-navbar-collapse-1">
									<span className="sr-only">
										Toggle navigation
						           </span>
									<span className="icon-bar">
									</span>
									<span className="icon-bar">
									</span>
									<span className="icon-bar">
									</span>
								</button>
								<Link to="/" className="navbar-brand header-logo" href="index.html"><img src="https://efrana.github.io/Tech_blogV1.2UI/img/logo.png" className="img-responsive"
									alt="Logo" /></Link>
							</div>
							<div className="collapse navbar-collapse" id="bs-example-navbar-collapse-1">
								<ul className="nav navbar-nav navbar-right">
									{authenticationMenu}
									{/* dropdown */}
                                   <li>
									<div class="dropdown">
										<a href="#" class="dropdown-toggle" data-toggle="dropdown"><i
											class="fa fa-align-justify dropbtn gearicon_new"></i></a>
										<ul class="dropdown-menu menudesign3">
										
											<li>
												<a href="#" class="mainMenu2">
													<i class="fa fa-home" style={{ color: "gray" }}></i>
													<Link to ="/">
													<span class="logout"> Home</span></Link>
													</a>
											</li>
											<br />
											<li>
												<a href="#" class="mainMenu2">
													<i class="fas fa-globe" style={{ color: "gray" }}></i>
													<Link to ="/question_list">
													<span class="logout" > Question List</span></Link>
													</a>
											</li>
											<br/>
											<li>
												<a href="#" class="mainMenu2">
													<i class="fa fa-question" style={{ color: "gray" }}></i>
													<Link to ="/ask_question">
													<span class="logout" > Ask Question</span></Link></a>
											</li>
											<br/>
											<li>
												<a href="#" class="mainMenu2">
													<i class="fa fa-tags" style={{ color: "gray" }}></i>
													<Link to ="/tags">
													<span class="logout" > Tags</span></Link></a>
											</li>
										</ul>
									</div>
									</li>
                              {/* dropdown */}

								</ul>
								<SearchBar />
							</div>
						</div>
					</div>
				</nav>
				<Login
					show={this.state.ModalOpen}
					manageState={() => {
						this.openModal()
					}}
				/>
				<Signup
					show={this.state.ModalOpen}
					manageState={() => {
						this.openModal()
					}}

				/>

			</React.Fragment>
		)
	}

}
export default withRouter(NavBar)